import React, { FunctionComponent } from 'react';
import styled from "@emotion/styled"
import searchImage from "../../../images/search.png"

const MainNavWrapR = styled.div`
  position: absolute;
  top: 20px; right: 20px;

  @media (min-width: 1200px) {
    position: static;
  }
`

const NavSearch = styled.img`
  width: 20px; height: 20px;
  @media (min-width: 1200px) {
    width: 40px; height: 40px;
    margin-right: 10px;
  }
`

const NavRight: FunctionComponent = function(){


  return (

    <MainNavWrapR>
      <NavSearch src={searchImage} />
    </MainNavWrapR>

  )
}

export default NavRight