import React, { FunctionComponent } from 'react';
import styled from "@emotion/styled"
import { keyframes } from '@emotion/react';
import logo from "../../../images/mainLogo.png"


const MainNavWrapL = styled.div`
  width: 100%; height: 100%;
  display: flex; flex-direction: column;

  @media (min-width: 1200px) {
    flex-direction: inherit; align-items: center;
  }
`
const LogoImage = styled.img`
  margin: 20px 0 20px 20px;
  width: 100px;

  @media (min-width: 1200px) {
    margin-left: 0px;
    margin-top: 0px;
    margin-right: 30px;
    width: 120px; height: 40px;
  }
`
const NavMenuWrap = styled.ul`
  display: flex;
  width: 100%; height: 100%;
  font-size : 13px;
`
const NavMenuList = styled.li`
  position: relative;
  display: flex; justify-content: center; align-items: center;
  width: 25%; height: 100%;
  text-align: center;
  z-index: 10;
  @media (min-width: 1200px) {
    width: 16%;
    font-size: 20px; font-weight: bold;
  }
`
const Hop = keyframes`
  0% {top: 0px;}
  100% {top: -10px;}
`
const Hop1200 = keyframes`
  0% {top: 20px;}
  100% {top: 15px;}
`;

const NavStyle = styled.div`
  position: absolute;
  top: 0px; left : 50%;

  border-radius: 15px;
  padding: 0 3px;
  font-size: 12px;
  color: #fff;
  background-color: red;
  transform: translateX(-50%);
  animation: ${Hop} 0.5s linear 1s infinite alternate;

  @media (min-width: 1200px) {
    font-size: 18px;
    top: 20px;
    animation: ${Hop1200} 0.5s linear 1s infinite alternate;
  }
`
const NavLeft: FunctionComponent = function(){

  const navMenuList = ["ABOUT", "작품", "고객센터", "실험실"]
  const navMenu = navMenuList.map((menu)=>(
    (menu === "작품") 
      ? <NavMenuList><NavStyle>new</NavStyle>{menu}</NavMenuList> 
      : <NavMenuList>{menu}</NavMenuList>
  ))

  return (
    <MainNavWrapL>
      <LogoImage src={logo} />
      <NavMenuWrap>{navMenu}</NavMenuWrap>
    </MainNavWrapL>
  )
}

export default NavLeft