import React, { FunctionComponent, ReactNode } from 'react'
import styled from '@emotion/styled'
import { Link } from 'gatsby'

export type CategoryListProps = {
  selectedCategory: string
  categoryList: string[]
}
type CategoryItemProps = {
  active: boolean;
}

type GatsbyLinkProps = {
  children: ReactNode;
  className?: string;
  to: string;
} & CategoryItemProps
// eslint-disable-next-line @typescript-eslint/no-unused-vars

const CategoryItem = styled(({ active, ...props }: GatsbyLinkProps) => (
  <Link {...props} />
))<CategoryItemProps>`
  margin-right: 15px;
  padding: 5px 5px;
  border: solid 1px #d3d3d3;
  border-radius: 30px;
  font-size: 15px;
  font-weight: ${({ active }) => (active ? '800' : '400')};
  cursor: pointer;
  background-color: ${({ active }) => (active ? 'pink' : '#fff')};
  color: ${({ active }) => (active ? 'white' : 'black')};
  &:last-of-type {
    margin-right: 0;
  }
`

const CategoryItemWrap = styled.div`
  display: flex;
  margin: 10px 10px;
  overflow: auto;
  -ms-overflow-style: none;
  ::-webkit-scrollbar { 
    display:none; 
  }

`

const CategoryList: FunctionComponent<CategoryListProps> = function ({
  selectedCategory,
  categoryList,
}) {

  const listItems = categoryList.map((category)  =>
    <CategoryItem to={`/?category=${category}`} key={category} active={category === selectedCategory}>{category}</CategoryItem>
  );

  return (
    <>
    <CategoryItemWrap>
      {listItems}
    </CategoryItemWrap>
        
    
    </>
  )
}

export default CategoryList